.nav{
    position: fixed;
    top:0px;
    padding: 10px;
    width : 100%;
    height:  30px;
    z-index: 1;
    transition-timing-function: ease-in ;
    transition: all 0.5s;
}




.nav__contents{
    display: flex;
    justify-content: space-between;
}

.nav__logo{
    position : fixed;
    left: 0;
    top: 8px;
    width: 150px;
    height: 40px;
    padding-left: 20px;
    cursor: pointer;
    
}

.nav__avatar{
    position: fixed;
    right: 20px;
    width: 30px;
    height: 30px;
    cursor: pointer;
}
.nav__black{
    background-color: #111;
}