.profileScreen{
    height: 100vh;
    color: white;

}

.profileScreen__body{
    display: flex;
    flex-direction: column;
    width: 50%;
    margin-left: auto;
    margin-right: auto;
    max-width: 800px;
}


.profileScreen__info{
    display: flex;
}

.profileScreen__info>img{
    height: 100px;
}


.profileScreen__body>h1{
    font-size: 60px;
    font-weight: 400;
    border-bottom: 1px solid #282c2d;
    margin-bottom: 20px;
}

.profileScreen__details{
    color: white;
    margin-left: 25px;
    flex: 1;
}

.profileScreen__details>h2{
    background-color: gray;
    padding: 15px;
    font-size: 15px;
    padding-left: 20px;
    border-radius: 10px;
}

.profileScreen__signOut{
    padding: 10px 20px;
    font-size: 1rem;
    margin-top: 5%;
    width: 100%;
    color: white;
    background-color: #e50914;
    border-radius: 10px;
    border: none;
    cursor: pointer;

}


.profileScreen__signOut:hover{
    background-color:  #ca1c24 ;
}